@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;700;900&display=swap);
*{padding:0;margin:0;box-sizing:border-box}html,body{width:100vw;height:100vh}body{margin:0;font-family:'Montserrat', sans-serif}code{font-family:'Montserrat', sans-serif}

.nav{position:-webkit-sticky;position:sticky;top:0;z-index:100;display:flex;justify-content:space-around;align-items:center;background-color:#2d2e32;color:#fff;padding:0.5rem}.nav .logo{font-weight:900;font-size:2rem;background:-webkit-linear-gradient(#ecdbb6, #cda55f);-webkit-background-clip:text;-webkit-text-fill-color:transparent}.nav .logo span{font-size:0.9rem;margin:0.2rem;font-weight:600}.nav nav .mobile{display:none}.nav nav .mobile .menu-icon{color:#cda55f;font-size:1.4rem;cursor:pointer;margin-left:1rem}.nav nav ul{display:flex;justify-content:space-around;align-items:center;flex-wrap:wrap;text-align:center;margin:0}.nav nav ul li{list-style:none;margin:0 2rem;font-weight:600;font-size:1rem;color:#dabb86}.nav nav ul .mail{background-color:#292a2e;width:50px;height:50px;border-radius:50%;display:flex;justify-content:center;align-items:center;box-shadow:0 5px 5px rgba(0,0,0,0.3);transition:all 0.3s ease-in-out;cursor:pointer}.nav nav ul .mail img{width:25px}.nav nav ul .mail:hover{box-shadow:none}.scrollEffect{box-shadow:0 10px 10px rgba(0,0,0,0.2)}@media (max-width: 800px){.nav{justify-content:space-between;padding:1rem 2rem !important}.nav .logo{font-size:1.5rem}.nav .logo span{font-size:0.9rem}.nav nav .mobile{display:flex;align-items:center}.nav nav .mobile button{font-size:0.9rem;border:none;background-image:linear-gradient(#ecdbb6, #cda55f);padding:0 0.4rem;border-radius:0.2rem;margin:0.5rem;box-shadow:0 5px 5px rgba(0,0,0,0.2)}.nav nav .main-menu{display:none}.nav nav .mobile-menu{display:flex;flex-direction:column;position:absolute;right:0;top:3.5rem;background-color:rgba(0,0,0,0.5);padding:1rem;border-radius:1rem 0 0 1rem;box-shadow:0 10px 10px rgba(0,0,0,0.1);transition:all 0.3s ease-in-out}.nav nav .mobile-menu li{margin:1rem;font-size:0.9rem;cursor:pointer}.nav nav .mobile-menu li:hover{color:#996e23;text-shadow:0 5px 5px rgba(0,0,0,0.5)}}

.header{width:100vw;min-height:80vh;background-color:#2d2e32;display:grid;grid-template-columns:repeat(2, 50vw);grid-template-areas:'text profile'}.header .text{padding:2rem;grid-area:text;display:flex;flex-direction:column;align-items:flex-start;justify-content:space-around;margin:5rem 0 7rem 5rem}.header .text h3{color:#000;font-size:1.2rem;background-color:#64f4ab;display:inline-block;padding:0.5rem 1rem;border-radius:0.3rem;box-shadow:0 10px 10px rgba(0,0,0,0.3);margin-right:7rem;transition:all 0.3s ease-in-out}.header .text h3:hover{box-shadow:none}.header .text h1{color:#fff;font-size:4rem}.header .text p{color:#999;font-size:1.3rem;font-weight:500;width:70%}.header .text a{color:#64f4ab;font-weight:600;background-color:#232427;box-shadow:0 10px 10px rgba(0,0,0,0.1);padding:0.5rem;font-size:1.1rem;border-radius:0.5rem;transition:all 0.3s ease-in-out;cursor:pointer}.header .text a:hover{box-shadow:none;text-decoration:none}.profile{display:flex;justify-content:center;align-items:center;padding:2rem}.profile .image{background-image:linear-gradient(#d89b16, #7c5718);padding:1rem;border-radius:50%;margin-bottom:5rem;box-shadow:0 15px 15px rgba(0,0,0,0.2);transition:all 0.3s ease-in-out}.profile .image img{border-radius:50%;width:400px}.profile .image:hover{box-shadow:none}.animate1{-webkit-animation:fadeIn1 3s forwards;animation:fadeIn1 3s forwards}@-webkit-keyframes fadeIn1{0%{opacity:0;transform:translateY(-4em)}100%{opacity:1;transform:translateY(0)}}@keyframes fadeIn1{0%{opacity:0;transform:translateY(-4em)}100%{opacity:1;transform:translateY(0)}}.animate2{-webkit-animation:fadeIn2 3s forwards;animation:fadeIn2 3s forwards}@-webkit-keyframes fadeIn2{0%{opacity:0;transform:translateY(4em)}100%{opacity:1;transform:translateY(0)}}@keyframes fadeIn2{0%{opacity:0;transform:translateY(4em)}100%{opacity:1;transform:translateY(0)}}.animate3{-webkit-animation:fadeIn3 3s forwards;animation:fadeIn3 3s forwards}@-webkit-keyframes fadeIn3{0%{opacity:0;transform:translateX(-5em)}100%{opacity:1;transform:translateX(0)}}@keyframes fadeIn3{0%{opacity:0;transform:translateX(-5em)}100%{opacity:1;transform:translateX(0)}}.animate4{-webkit-animation:fadeIn4 3s forwards;animation:fadeIn4 3s forwards}@-webkit-keyframes fadeIn4{0%{opacity:0;transform:translateX(5em)}100%{opacity:1;transform:translateX(0)}}@keyframes fadeIn4{0%{opacity:0;transform:translateX(5em)}100%{opacity:1;transform:translateX(0)}}@media (max-width: 1150px){.header{grid-template-columns:100vw;grid-template-areas:'text' 'profile'}.header .text{padding:3rem}.header .text h3{font-size:0.9rem}.header .text h1{margin:1rem 0;font-size:3rem}.header .text p{margin:2rem 0;font-size:1.2rem;width:70%}.header .text a{font-size:1rem}.profile .image img{width:300px}}@media (max-width: 900px){.header .text{align-items:center;text-align:center;padding:3rem;margin-left:0;margin-bottom:0}.header .text h3{font-size:0.9rem;margin:auto}.header .text h1{margin:2rem auto;font-size:2rem}.header .text p{margin:1rem auto;font-size:1rem;width:90%}.header .text a{margin:auto}.profile .image img{width:200px}}

.about-block{background-color:#25262a;width:100vw;min-height:100vh;display:grid;grid-template-columns:40% 60%;grid-template-areas:'skills intro';padding:4rem 0 2rem}.about-block .skills{grid-area:skills;padding:2rem;margin:2rem}.about-block .skills h3{margin:1rem;background:-webkit-linear-gradient(#ecdbb6, #cda55f);-webkit-background-clip:text;-webkit-text-fill-color:transparent}.about-block .skills .skill{font-size:0.9rem;text-align:center;font-weight:500;color:#cda55f;background-color:#2d2e32;display:inline-block;border-radius:0.5rem;box-shadow:0 10px 10px rgba(0,0,0,0.2);margin:1rem;padding:1rem 1rem 0.5rem;transition:all 0.3s ease-in-out}.about-block .skills .skill:hover{box-shadow:none}.about-block .intro{grid-area:intro;padding:2rem;margin:2rem}.about-block .intro h4{margin:2rem 0;color:#999;font-weight:600;background:-webkit-linear-gradient(#ecdbb6, #cda55f);-webkit-background-clip:text;-webkit-text-fill-color:transparent;text-shadow:0 5px 5px rgba(0,0,0,0.2)}.about-block .intro h1{margin:4rem 0;color:#fff;font-weight:700}.about-block .intro h1 span{margin:0 0 4rem;display:block;font-weight:500;color:#cda55f}.about-block .intro h3{color:#fff;font-size:1.5rem;margin:1rem 0 2rem}.about-block .intro p{width:80%;color:#999;font-size:1.2rem;font-weight:500;line-height:2.5rem}@media (max-width: 1100px){.about-block{text-align:center;grid-template-columns:100%;grid-template-areas:'intro' 'skills'}.about-block .intro h4{font-size:1.3rem}.about-block .intro h1{font-size:2rem}.about-block .intro h3{font-size:1.3rem}.about-block .intro p{font-size:1rem;width:100%}}@media (max-width: 500px){.about-block .skills{padding:0;margin:0}}

.product{width:400px;text-align:center;background-color:#2d2e32;padding:1rem;border-radius:1rem;box-shadow:0 10px 10px rgba(0,0,0,0.3);transition:all 0.3s ease-in-out;margin:1rem}.product:hover{box-shadow:none}.product h2{font-size:1.5rem;color:#cda55f}.product img{width:350px;border-radius:0.5rem;border:2px solid #cda55f;margin:0.5rem}.product a{text-transform:lowercase;display:inline-block;margin:0.5rem;background-color:#cda55f;color:#000;padding:0.5rem;border-radius:0.2rem;box-shadow:0 10px 10px rgba(0,0,0,0.3);text-decoration:none;font-weight:500;transition:all 0.3s ease-in-out}.product a:hover{box-shadow:none}.animate1{-webkit-animation:fadeIn1 3s forwards;animation:fadeIn1 3s forwards}@-webkit-keyframes fadeIn1{0%{opacity:0;transform:translateY(-4em)}100%{opacity:1;transform:translateY(0)}}@keyframes fadeIn1{0%{opacity:0;transform:translateY(-4em)}100%{opacity:1;transform:translateY(0)}}.animate2{-webkit-animation:fadeIn2 3s forwards;animation:fadeIn2 3s forwards}@-webkit-keyframes fadeIn2{0%{opacity:0;transform:translateY(4em)}100%{opacity:1;transform:translateY(0)}}@keyframes fadeIn2{0%{opacity:0;transform:translateY(4em)}100%{opacity:1;transform:translateY(0)}}.animate3{-webkit-animation:fadeIn3 3s forwards;animation:fadeIn3 3s forwards}@-webkit-keyframes fadeIn3{0%{opacity:0;transform:translateX(-5em)}100%{opacity:1;transform:translateX(0)}}@keyframes fadeIn3{0%{opacity:0;transform:translateX(-5em)}100%{opacity:1;transform:translateX(0)}}.animate4{-webkit-animation:fadeIn4 3s forwards;animation:fadeIn4 3s forwards}@-webkit-keyframes fadeIn4{0%{opacity:0;transform:translateX(5em)}100%{opacity:1;transform:translateX(0)}}@keyframes fadeIn4{0%{opacity:0;transform:translateX(5em)}100%{opacity:1;transform:translateX(0)}}@media (max-width: 500px){.product{width:100%;padding:1rem;margin:1rem 0}.product h2{font-size:1.2rem}.product img{padding:0;margin:0;width:90%}.product a{font-size:0.8rem;padding:0.3rem}}

.works{width:100vw;background-color:#25262a;padding:4rem}.works h2{margin:2rem;text-align:center;background:-webkit-linear-gradient(#ecdbb6, #cda55f);-webkit-background-clip:text;-webkit-text-fill-color:transparent}.works .project-container{display:flex;justify-content:space-around;flex-wrap:wrap}.works button{background-color:#64f4ab;color:#000;padding:0.5rem 1rem;border-radius:0.5rem;font-size:1.1rem;border:none;box-shadow:0 10px 10px rgba(0,0,0,0.2);display:block;margin:4rem auto 2rem;transition:all 0.3s ease-in-out}.works button:hover{box-shadow:none;border:1px solid #64f4ab;color:#64f4ab;background-color:#25262a}@media (max-width: 500px){.works{padding:1rem}.works h2{font-size:1.5rem}.works .project-container .project{width:100%;padding:0.5rem}.works .project-container .project img{width:100%}.works .project-container .project h3{font-size:1.2rem}.works .project-container .project .tools div{font-size:0.9rem}}

.contact{background-color:#25262a;width:100vw;display:grid;grid-template-columns:repeat(2, 50vw);grid-template-areas:'info social';border-top:1px solid #444;padding:4rem 0 1rem}.contact .info{text-align:center;grid-area:info;padding:1rem}.contact .info h3{font-size:1.5rem;margin:2rem;text-align:center;background:-webkit-linear-gradient(#ecdbb6, #cda55f);-webkit-background-clip:text;-webkit-text-fill-color:transparent}.contact .info .email{color:#fff;font-weight:600}.contact .info .email a{color:#cda55f}.contact .info .footer{color:#fff}.contact .social{grid-area:social;text-align:center}.contact .social h3{font-size:1.5rem;margin:2rem;text-align:center;background:-webkit-linear-gradient(#ecdbb6, #cda55f);-webkit-background-clip:text;-webkit-text-fill-color:transparent}.contact .social .social-icons{background-color:#cda55f;display:inline-block;padding:0.5rem;border-radius:0.5rem;box-shadow:0 10px 10px rgba(0,0,0,0.2)}.contact .social .social-icons img{margin:0 1rem;width:30px}@media (max-width: 600px){.contact{padding:1rem;grid-template-columns:100%;grid-template-areas:'social' 'info'}.contact .info{margin:1rem 0}.contact .social{margin:1rem 0}.contact .social .social-icons{padding:0.5rem}.contact .social .social-icons img{width:20px;margin:0 0.5rem}}

*{padding:0;margin:0}.about{background-color:#25262a;padding:2rem;display:flex;flex-wrap:wrap;justify-content:space-around}.about .skills-section{background-color:#2d3037;padding:2rem;border-radius:1rem;width:40vw;margin:2rem;box-shadow:0 10px 10px rgba(0,0,0,0.3)}.about .skills-section h2{margin:2rem 0;background:-webkit-linear-gradient(#ecdbb6, #cda55f);-webkit-background-clip:text;-webkit-text-fill-color:transparent}.about .skills-section p{color:#fff;font-weight:500}.about .learning-section{background-color:#191c21;width:40vw;padding:2rem;border-radius:1rem;box-shadow:0 10px 10px rgba(0,0,0,0.3);margin:2rem}.about .learning-section h2{background:-webkit-linear-gradient(#ecdbb6, #cda55f);-webkit-background-clip:text;-webkit-text-fill-color:transparent;margin:2rem 0}.about .learning-section p{color:#fff;font-weight:500}.about .learning-section p span{color:#dabd85;font-weight:600}.about .certificates-section{background-color:#2d3037;padding:2rem;border-radius:1rem;margin:2rem 0;width:90%;box-shadow:0 10px 10px rgba(0,0,0,0.3)}.about .certificates-section h2{text-align:center;background:-webkit-linear-gradient(#ecdbb6, #cda55f);-webkit-background-clip:text;-webkit-text-fill-color:transparent;margin:2rem 2rem}.about .certificates-section .images{display:flex;justify-content:center;flex-wrap:wrap;padding:1rem;border-radius:1rem}.about .certificates-section .images img{display:block;box-shadow:0 5px 10px rgba(0,0,0,0.2);width:300px;border-radius:1rem;margin:1rem;box-shadow:0 5px 10px rgba(0,0,0,0.2);transition:all 0.3s ease-in-out}.about .certificates-section .images img:hover{border:4px solid #cda55f}.animate1{-webkit-animation:fadeIn1 3s forwards;animation:fadeIn1 3s forwards}@-webkit-keyframes fadeIn1{0%{opacity:0;transform:translateY(-4em)}100%{opacity:1;transform:translateY(0)}}@keyframes fadeIn1{0%{opacity:0;transform:translateY(-4em)}100%{opacity:1;transform:translateY(0)}}.animate2{-webkit-animation:fadeIn2 3s forwards;animation:fadeIn2 3s forwards}@-webkit-keyframes fadeIn2{0%{opacity:0;transform:translateY(4em)}100%{opacity:1;transform:translateY(0)}}@keyframes fadeIn2{0%{opacity:0;transform:translateY(4em)}100%{opacity:1;transform:translateY(0)}}.animate3{-webkit-animation:fadeIn3 3s forwards;animation:fadeIn3 3s forwards}@-webkit-keyframes fadeIn3{0%{opacity:0;transform:translateX(-5em)}100%{opacity:1;transform:translateX(0)}}@keyframes fadeIn3{0%{opacity:0;transform:translateX(-5em)}100%{opacity:1;transform:translateX(0)}}.animate4{-webkit-animation:fadeIn4 3s forwards;animation:fadeIn4 3s forwards}@-webkit-keyframes fadeIn4{0%{opacity:0;transform:translateX(5em)}100%{opacity:1;transform:translateX(0)}}@keyframes fadeIn4{0%{opacity:0;transform:translateX(5em)}100%{opacity:1;transform:translateX(0)}}@media (max-width: 950px){.about .skills-section,.about .learning-section{width:100vw}.about .certificates-section .images{padding:0}.about .certificates-section .images img{width:100%;margin:1rem 0}}@media (max-width: 600px){.about .skills-section,.about .learning-section,.about .certificates-section{margin:1rem 0}.about .skills-section h2,.about .learning-section h2,.about .certificates-section h2{font-size:1.5rem}.about .skills-section p,.about .learning-section p,.about .certificates-section p{width:100%;font-size:1rem}}

.projects{background-color:#25262a;min-height:100vh;padding:2rem;display:flex;justify-content:space-around;align-items:flex-start;flex-wrap:wrap}

